.header {
  background: #131921;
  position: sticky;
  top: 0;
  z-index: 1000;
}
.header_logo {
  margin: 18px 20px 0 20px;
  width: 100px;
  object-fit: contain;
}
.inputBox {
  flex: 1;
}
.header_searchBox {
  height: 12px;
  padding: 12px;
  border: none;
  outline: none;
  width: 100%;
  font-family: "Ubuntu", sans-serif;
  font-size: 13px;
}
.header_searchIcon {
  background: #cd9042;
  padding: 5px;
  cursor: pointer;
  margin-left: 5px;
}
.header_optionLineOne {
  font-size: 10px;
}
.header_optionLineTwo {
  font-size: 13px;
  font-weight: 800;
}
